@import "normalize.css";
@import "variables";
@import "nav";
@import "home";
@import "buttons";
@import "worklist";
@import "footer";



html {
	box-sizing: border-box;
}

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-family: $mono;
	color: $text-color;
	background-color: white;
	font-size: 16px;
	transition: background-color .25s ease-in-out;

	@media(min-width: $breakpoint-md) {
		font-size: 18px;
	}

	@media(min-width: $breakpoint-lg) {
		font-size: 20px;
	}

	@media(min-width: $breakpoint-xl) {
		font-size: 22px;
	}

	/* width */
	&::-webkit-scrollbar {
		width: 8px;
		background: white;
	}

	/* Track */
	&::-webkit-scrollbar-track {
		background: white;
	}

	/* Handle */
	&::-webkit-scrollbar-thumb {
		background: rgba($text-color, .4);
		width: 6px;

		&:hover {
			background: rgba($text-color, .6);
		}
	}

	&.shorthand {
		background-color: $shorthand;
	}

	&.viator {
		background-color: $viator;
	}

	&.tgm {
		background-color: $tgm;
	}

	&.aus {
		background-color: $aus;
	}
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

#portfolio {
	width: 100%;
	overflow-x: hidden;
}

a,
a:visited {
	text-decoration: none;
	color: $white;
}

ul {
	padding: 0;
	margin: 0;
}

p {
	a {
		color: $text-color;
		border-bottom: 1px dotted;

		&:hover {
			border-bottom: 1px solid;
		}

		&:not(.btn):visited {
			color: $text-color-alt;
		}
	}
}

h3 {
	font-size: 100%;

	@media (min-width: $breakpoint-md) {
		font-size: 120%;
	}

	@media (min-width: $breakpoint-lg) {
		font-size: 160%;
	}
}

h5 {
	font-size: 100%;
	font-family: $title;
	font-weight: 900;
	text-transform: uppercase;
	letter-spacing: .1em;
	padding: 2em 0;
	margin: 0 auto;
}

img {
	display: block;
}



.centered {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	-webkit-transform: translate(-50%, -50%);
	width: 90%;
	z-index: 5;
}

header {
	text-align: center;
	// padding: 65% 0 0 ;
	overflow: hidden;
	display: block;
	position: relative;
	z-index: 0;
	height: 75vh;
	// min-height: 500px;

	h1 {
		margin: 0 auto .5em;
		font-family: $title;
		line-height: 1.3;
		text-transform: uppercase;
		font-weight: 900;
		letter-spacing: .35em;
		font-size: calc(100% + 1.5vw);

		@media (orientation:landscape) {
			font-size: calc(100% + 1.25vw);
		}
	}

	h2 {
		margin: 0;
		font-weight: normal;
		font-size: calc(100% + .15vw);

		span {
			color: rgba($text-color, .4);

			.work & {
				color: rgba($white, .4);
			}
		}
	}

	&.sub {
		color: $white;
		box-shadow: inset 0 -10px 15px -15px black;

		.shorthand & {
			background: $shorthand url('../Media/index-shorthand.jpg') center no-repeat;
			background-attachment: fixed;
			background-size: cover;
		}

		.viator & {
			background: $viator url('../Media/index-viator.jpg') center no-repeat;
			background-attachment: fixed;
			background-size: cover;
		}

		.tgm & {
			background: $tgm url('../Media/index-tgm.jpg') center no-repeat;
			background-attachment: fixed;
			background-size: cover;
		}

		.aus & {
			background: $aus url('../Media/index-aus.jpg') center no-repeat;
			background-attachment: fixed;
			background-size: cover;
		}

		&:after,
		&:before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}

		&:before {
			z-index: 1;
			background-color: rgba(black, 0.3);
		}

		&:after {
			background-color: $white;
			opacity: .7;
			z-index: 2;



			.shorthand & {
				background-color: $shorthand;
			}

			.viator & {
				background-color: $viator;
			}

			.tgm & {
				background-color: $tgm;
			}

			.aus & {
				background-color: $aus;
			}
		}
	}
}

.work-container {
	background-color: white;
}

#intro {
	display: block;
	text-align: center;
	width: 90%;
	margin: 5% auto 0;

	@media (min-width: $breakpoint-sm) {
		width: 65%;
		max-width: 1000px;
		min-width: 290px;
	}

	h3 {
		font-size: 240%;
		margin-top: 10%;
		margin-bottom: 10px;
		font-family: $title;
		font-weight: 900;
		text-transform: uppercase;
		letter-spacing: .3em;
	}

	h4 {
		font-size: 160%;
		opacity: .4;
		font-weight: 100;
		margin: 0 0 5%;
	}

	p {
		font-size: 110%;
		line-height: 1.4;

		&:first-of-type {
			font-weight: bold;
			font-size: 140%;
		}
	}

	.date {
		opacity: .4;
	}
}

.example {
	text-align: center;
	padding: 2% 5%;
	max-width: 1600px;
	margin: 0 auto;

	img {
		width: 100%;
		height: auto;
		// max-width: 1400px;
		// min-width: 300px;
		// margin: 0 auto 1rem;
	}

	.bordered {
		border: $borderWidth solid #f1f1f1;
		margin: 0 auto 3rem;
		position: relative;

		img {
			margin: 0;
		}

		p {
			width: auto;
			display: block;
			margin: 0;
			position: absolute;
			bottom: -$borderWidth;
			left: -$borderWidth;
			right: -$borderWidth;
			background-color: #f1f1f1;
			padding: 1rem 20% 1rem;
			opacity: .9;
			width: calc(100% + #{$borderWidth * 2});
			min-width: calc(100% + #{$borderWidth * 2});
		}
	}

	p {
		width: 60%;
		max-width: 800px;
		min-width: 250px;
		margin: 1rem auto 3rem;
	}

	.btn {
		margin-top: -1.5rem;
	}

	&.grid {
		ul {
			padding: 0;
			margin-bottom: 20px;
			list-style: none;
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: center;
			align-items: flex-start;
			align-content: flex-start;
			display: -webkit-flex;
			-webkit-flex-direction: row;
			-webkit-flex-wrap: wrap;
			-webkit-justify-content: center;
			-webkit-align-items: flex-start;
			-webkit-align-content: flex-start;

			&.quarter {
				li {
					width: 25%;
				}
			}

			li {
				width: 50%;
				background-color: $text-color;
				display: inline-block;
				position: relative;
				border: 3px solid $white;

				a {
					position: relative;
					display: block;
					overflow: hidden;

					img {
						width: 100%;
						margin: 0;
						display: block;
					}

					.overlay {
						position: absolute;
						z-index: 2;
						text-align: center;
						width: 100%;
						height: 100%;
						background-color: rgba(black, 0.8);
						opacity: 0;
						transition: opacity 100ms ease-in-out;

						span {
							margin-top: 20%;
							display: block;
						}

						.btn {
							margin-top: 15px;
						}

						&:hover {
							opacity: 1;
							transition: opacity 100ms ease-in-out;
						}
					}
				}
			}
		}
	}
}



