.page {
	transition: transform .25s ease-in-out, filter .15s ease-in-out;
	width: 100%;
	overflow-x: hidden;
}

.page-enter {
	transform: translateX(-100vw);
	position: fixed;
	// opacity: 0;

	nav {
		transform: translateY(-100px);
	}
}

.page-enter-active {
	transform: translateX(0);
	// opacity: 1;
}

.page-exit {
	transform: translateX(-100vw);
	position: fixed;
	// opacity: 0;
	filter: brightness(50%);

	nav {
		transform: translateY(-100px);
	}
}

.page-exit-active {
	// opacity: 0;
	filter: brightness(50%);
}

.page--prev.page-enter {
	transform: translateX(100vw);
}

.page--prev.page-enter-active {
	transform: translateX(0);
}

.page--prev.page-exit {
	transform: translateX(100vw);
}